import React from "react";

const Navbar = ({ onMenuClick }) => {
  return (
    <div className="navbar">
      <button onClick={() => onMenuClick("Users")}>Users</button>
      <button onClick={() => onMenuClick("TaskLists")}>Task Lists</button>
      <button onClick={() => onMenuClick("Tasks")}>Tasks</button>
    </div>
  );
};

export default Navbar;
