import React from "react";
import DataGrid from "../components/DataGrid";

const TaskLists = () => {
  const rows = [
    { id: 1, title: "Project Alpha", createdBy: "user1@example.com", noOfTasks: 10, creationTime: "2024-11-20", lastUpdated: "2024-11-23" },
    { id: 2, title: "Project Beta", createdBy: "user2@example.com", noOfTasks: 5, creationTime: "2024-11-21", lastUpdated: "2024-11-24" },
  ];

  const columns = [
    { field: "title", headerName: "Task List Title", width: 200 },
    { field: "createdBy", headerName: "Created By", width: 200 },
    { field: "noOfTasks", headerName: "No of Tasks", width: 150 },
    { field: "creationTime", headerName: "Creation Time", width: 200 },
    { field: "lastUpdated", headerName: "Last Updated", width: 200 },
  ];

  return <DataGrid rows={rows} columns={columns} />;
};

export default TaskLists;
