import React from "react";
import DataGrid from "../components/DataGrid";

const Users = () => {
  const rows = [
    { id: 1, email: "user1@example.com", password: "123456", signupTime: "2024-11-25", ip: "192.168.0.1" },
    { id: 2, email: "user2@example.com", password: "abcdef", signupTime: "2024-11-24", ip: "192.168.0.2" },
  ];

  const columns = [
    { field: "email", headerName: "Email ID", width: 200 },
    { field: "password", headerName: "Password", width: 150 },
    { field: "signupTime", headerName: "Signup Time", width: 200 },
    { field: "ip", headerName: "IP", width: 150 },
  ];

  return <DataGrid rows={rows} columns={columns} />;
};

export default Users;
