import React from "react";
import DataGrid from "../components/DataGrid";

const Tasks = () => {
  const rows = [
    { id: 1, title: "Task A", description: "Description A", taskListTitle: "Project Alpha", createdBy: "user1@example.com", creationTime: "2024-11-20" },
    { id: 2, title: "Task B", description: "Description B", taskListTitle: "Project Beta", createdBy: "user2@example.com", creationTime: "2024-11-21" },
  ];

  const columns = [
    { field: "title", headerName: "Task Title", width: 200 },
    { field: "description", headerName: "Task Description", width: 300 },
    { field: "taskListTitle", headerName: "Task List Title", width: 200 },
    { field: "createdBy", headerName: "Created By", width: 200 },
    { field: "creationTime", headerName: "Creation Time", width: 200 },
  ];

  return <DataGrid rows={rows} columns={columns} />;
};

export default Tasks;
